*{box-sizing:border-box}body{box-sizing:border-box;margin:0;padding:0}.home__container{display:grid;grid-template-rows:calc(100vh - 70px) 100vh;background-color:white}.home__container .home__banner--section{display:flex;flex-direction:column;justify-content:center;align-items:center}.home__container .home__banner--section .banner__background{position:absolute;left:0;top:0;z-index:0;display:flex;height:100%;width:100%;background-image:url(/assets/0f1fbead3f37186ea68adcce98442b39.jpg);background-size:cover;filter:brightness(30%)}.home__container .home__banner--section .banner__main--title{color:#eee;z-index:4;font-size:3em;font-family:"Oxanium",cursive}.home__container .home__banner--section .banner__main--subtitle{font-family:"Kaushan Script",cursive;font-weight:lighter;color:#90BE6D;z-index:4;font-size:1.5em;margin-top:-1em}.home__container .home__services--section{display:flex;flex-direction:column;align-items:center;justify-content:center;align-content:center;justify-items:center;height:100vh;padding:1em 1em;font-size:1.8em;color:#393e46;font-family:"Oxanium",cursive}.home__container .home__services--section .section__title{max-width:700px;margin-bottom:50px}.home__container .home__services--section .services__wrapper{display:flex;justify-content:space-between;max-width:1100px}.home__container .home__services--section .services__wrapper .service__wrapper{display:flex;flex-direction:column;align-items:center;width:30%;justify-content:center}.home__container .home__services--section .services__wrapper .service__wrapper .service__img{position:relative;display:flex;height:200px;width:200px;background-color:#90BE6D;border-radius:100%;background-size:cover;background-position:center;border:5px solid white;box-shadow:0px 3px 5px rgba(0,0,0,0.5);transition:all .2s ease-in;cursor:pointer}.home__container .home__services--section .services__wrapper .service__wrapper .service__img:hover{transform:scale(1.1)}.home__container .home__services--section .services__wrapper .service__wrapper .service__info{overflow:hidden}.home__container .home__services--section .services__wrapper .service__wrapper .service__info .service__title{font-size:1em;width:100%;text-align:center;color:#90BE6D;font-family:"Oxanium",cursive}.home__container .home__services--section .services__wrapper .service__wrapper .service__info .service__content{text-align:justify;font-size:.5em;font-family:"Oxanium",cursive}.home__container .home__contact--section{display:none;background-color:#393e46}
