*{box-sizing:border-box}body{box-sizing:border-box;margin:0;padding:0}body{background-color:#eee}.loginview__container{display:flex;flex-direction:column;justify-content:center;align-items:center;width:100vw;height:100vh}.form__container{display:flex;width:350px;height:500px;flex-direction:column;align-items:center;justify-content:center;justify-items:center;align-content:center;padding:40px;background-color:#fff;box-shadow:0px 5px 5px rgba(0,0,0,0.2)}.form__container .icon__section{display:flex;flex-direction:column;justify-content:center;align-items:center}.form__container .icon__section .icon__wrapper{display:flex;justify-content:center;align-items:center;height:150px;width:150px;background-color:#eee;border-radius:100%}.form__container .icon__section .icon__wrapper .login__icon{height:60px;width:60px;color:#717171}.form__container .icon__section .icon__text{font-family:"Oxanium",cursive;font-size:15px;color:#90BE6D}.form__container .inputs__section{display:flex;width:100%;flex-direction:column}.form__container .inputs__section .input{display:block;width:100%;font-family:"Oxanium",cursive;font-size:16px;padding:20px;height:40px;margin-bottom:20px;border-radius:5px;background-color:#eee;outline:none;border:none}.form__container .inputs__section .input:focus{outline:solid 1px #90BE6D}.form__container .inputs__section .submit__button{height:40px;background-color:#90BE6D;outline:none;border:none;color:#fff;border-radius:5px;cursor:pointer;font-family:"Oxanium",cursive;font-size:15px}
