*{box-sizing:border-box}body{box-sizing:border-box;margin:0;padding:0}::-webkit-scrollbar{display:none}.main__container{display:flex;justify-content:flex-end;width:100%;min-height:100vh;background-color:white}.main__container .hamburguer__control--wrapper{position:fixed;display:flex;left:0;top:0;height:40px;width:40px;transition:all .2s ease-in;background-color:#90BE6D}.main__container .hamburguer__control--wrapper #h__check{display:none}.main__container .hamburguer__control--wrapper .hamburguer__label{display:flex;justify-content:center;align-items:center;width:100%;height:100%;font-size:30px;cursor:pointer}.main__container .hamburguer__control--wrapper .hamburguer__icon{color:white}.main__container .open{left:300px}.main__container .sidenav__main--wrapper{display:flex;flex-direction:column;background-color:#222831;position:fixed;height:100%;max-height:100vh;transition:all .2s ease-in;min-width:300px}.main__container .sidenav__main--wrapper .user__section{display:flex;flex-direction:column;align-items:center;background-color:#393e46;height:260px;padding:1em 0}.main__container .sidenav__main--wrapper .user__section .user__avatar{width:135px;height:135px;border-radius:100%;border:solid 3px #90BE6D;background-color:#717171}.main__container .sidenav__main--wrapper .user__section .user__name{font-family:"Oxanium",cursive;font-size:20px;color:#eee;font-weight:lighter}.main__container .sidenav__main--wrapper .user__section .user__jobtitle{margin-top:-17px;padding:0;font-family:"Oxanium",cursive;color:#90BE6D;font-size:16px;font-weight:lighter}.main__container .sidenav__main--wrapper .user__section .user__rol{margin-top:-12px;font-family:"Oxanium",cursive;font-weight:lighter;font-size:12px;color:#717171}.main__container .sidenav__main--wrapper .modules__section{display:flex;flex-direction:column;max-height:calc(100vh - 260px);overflow-x:visible;overflow-y:scroll}.main__container .show{left:0}.main__container .hide{left:-300px}.main__container .main__content{display:flex;padding:0 2em 2em 0;width:calc(100% - 340px);transition:all .2s ease-in}.main__container .content__expanded{width:calc(100% - 50px)}
