*{box-sizing:border-box}body{box-sizing:border-box;margin:0;padding:0}.home__footer{display:flex;justify-content:center;background-color:#222831;height:250px;width:100%}.home__footer .footer__wrapper{display:grid;grid-template-columns:repeat(3, 1fr);gap:4em;height:250px;min-width:1100px;max-width:1100px;padding:1em}.home__footer .footer__wrapper .footer__column{display:flex;flex-direction:column}.home__footer .footer__wrapper .footer__column .footer__title{font-family:"Oxanium",cursive;font-weight:lighter;font-size:1em;color:#90BE6D}.home__footer .footer__wrapper .footer__column .footer__list{display:flex;flex-direction:column;margin:0;padding:0}.home__footer .footer__wrapper .footer__column .footer__list--item{list-style:none;color:#eee;font-family:"Oxanium",cursive;font-size:.8em;margin-bottom:.5em}
